.main-footer {
  position: relative;
  top: -36px;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  background-color: #fff;
}

.main-footer__bg-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 36px;
  padding-top: 5px;
  padding-bottom: 14px;
  background-color: #fff;
}

.main-footer__social-icons {
  display: flex;
  flex-wrap: wrap;
  padding-top: 40px;
  padding-bottom: 37px;
}

.main-footer__social-icon {
  width: 45px;
  height: 45px;
  border-radius: 100%;
  background-color: $light-gold;
  transition: $default-transition;
  &:hover {
    background-color: $light-mango;
    transition: $default-transition;
  }
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
}

.main-footer__copyright a {
  width: 100%;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid rgba(68, 68, 68, 0.05);
  padding-top: 14px;
  color: #444;
}

.main-footer__copyright-text {
  margin-right: auto;
}

@media (min-width: $tablet-layout-width) {
  .main-footer__bg-wrap {
    flex-direction: row;
    justify-content: space-between;
    padding: 11px 40px 45px;
  }

  .main-footer__copyright {
    width: auto;
    display: flex;
    align-items: center;
  }

  .main-footer__copyright-text {
    margin-right: 25px;
    font-size: 20px;
  }

  .main-footer__social-icon {
    width: 60px;
    height: 60px;
  }

  .main-footer__social-icons {
    padding: 0;
  }

  .main-footer__social-icon--facebook {
    width: 14px;
    height: 28px;
  }

  .main-footer__social-icon--vkontakte {
    width: 31px;
    height: 16px;
  }

  .main-footer__social-icon--instagram {
    width: 24px;
    height: 24px;
  }
}
