.s-add-profile {
  background-color: $dark-blue-grey; 
}

.s-add-profile__wrap {
  position: relative;
  background-color: $light-grey;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  padding-top: 50px;
}

.s-add-profile__caption {
  max-width: 100px;
}

.s-add-profile__currency-img {
  position: absolute;
  top: 40px;
  right: 25px;
}

.s-add-profile__rates-list, .s-add-profile__business-rates-list {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}

.s-add-profile__rates-item, .s-add-profile__business-rates-item {
  position: relative;
  display: flex;
  flex-direction: column;
  color: #444;
  font-weight: 300;
  border-bottom: 1px solid rgba($color: #444, $alpha: 0.05);
  padding: 14px 0;
  margin-right: 10px;
  &::after {
    position: absolute;
    top: 3px;
    right: -44px;
    width: 34px;
    height: 34px;
    border-radius: 34px;
    color: #fff;
    text-align: center;
    line-height: 34px;
  }
}

.s-add-profile__rates-item:first-child, .s-add-profile__business-rates-item:first-child {
  border-top: 1px solid rgba($color: #444, $alpha: .05);
}

.s-add-profile__rates-item:nth-child(1) {
  &::after {
    content: "1";
    background-color: $mango;
  }
}
.s-add-profile__rates-item:nth-child(2) {
  &::after {
    content: "2";
    background-color: $light-mango;
  }
}
.s-add-profile__rates-item:nth-child(3) {
  &::after {
    content: "3";
    background-color: $light-gold;
  }
}

.s-add-profile__show-business-rates-btn {
  width: 100%;
  margin-top: 45px;
  margin-bottom: 8px;
  font-weight: bold;
  color: $dark-blue-grey;
  font-size: 16px;
}

.s-add-profile__business-rates {
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  width: 85%;
  margin: 0 auto;
  z-index: 2;
  padding-top: 33px;
  border-radius: 20px;
  h2 {
    text-align: center;
    margin-left: 25px;
    margin-right: 25px;
  }
}

.s-add-profile__business-rates-list {
  margin: 30px 25px 0;
}

.s-add-profile__close-business-rates-btn {
  width: 100%;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #f3f4f8;
  color: $dark-blue;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  text-transform: uppercase;
  padding: 21px 25px;
  margin-top: 33px;
}

@media (min-width: $tablet-layout-width) {
  .s-add-profile__wrap {
    padding-top: 75px;
    .caption-indent {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  .s-add-profile__ctn-wrap {
    padding: 0 25px;
  }

  .s-add-profile__caption {
    max-width: 100%;
    text-align: center;
  }

  .s-add-profile__rates-list {
    position: relative;
    margin-left: 60px;
    margin-top: 73px;
    &::after {
      content: "";
      position: absolute;
      bottom: -35px;
      right: 0;
      width: 198px;
      height: 285px;
      background-color: #e2e5f2;
      border-radius: 20px;
      z-index: 1;
    }
  }

  .s-add-profile__rates-item {
    font-size: 28px;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 0;
    padding: 16px 25px;
    padding-right: 35px;
    z-index: 2;
    &::after {
      right: 100%;
      width: 67px;
      height: 67px;
      line-height: 67px;
    }
  }
  
  .s-add-profile__currency-img {
    top: auto;
    bottom: 40px;
    right: 110px;
    z-index: 3;
  }

  .s-add-profile__show-business-rates-btn {
    position: relative;
    width: auto;
    font-size: 20px;
    padding-left: 90px;
    margin-left: 10px;
    &::before {
      content: "";
      position: absolute;
      bottom: 50%;
      left: 0;
      width: 67px;
      height: 67px;
      background: #e2e5f2 url("../../img/business.svg") no-repeat center;
      background-size: 17px 21px;
      border-radius: 100%;
      transform: translateY(50%);
    }
  }

  .s-add-profile__business-rates {
    padding-top: 52px;
    z-index: 5;
  }

  .s-add-profile__business-rates-list {
    padding-right: 65px;
    padding-left: 65px;
  }

  .s-add-profile__business-rates-item {
    font-size: 22px;
    flex-direction: row;
    justify-content: space-between;
    padding: 16px 0;
  }
  
  .s-add-profile__close-business-rates-btn {
    margin-top: 70px;
    padding-top: 40px;
    padding-bottom: 43px;
    font-size: 24px;
    text-transform: none;
  }
}


@media (min-width: $desktop-layout-width) {
  .s-add-profile__wrap {
    padding-bottom: 165px;
  }

  .s-add-profile__ctn-wrap {
    padding: 0 107px;
  }

  .s-add-profile__wrap .caption-indent {
    padding-right: 100px;
  }

  .s-add-profile__rates-list {
    margin-top: 150px;
    &::after {
      width: 444px;
      height: 300px;
      bottom: -45px;
    }
  }

  .s-add-profile__rates-item {
    margin-right: 160px;
  }

  .s-add-profile__currency-img {
    width: 198px;
    height: 198px;
    bottom: 245px;
    right: 120px;
  }

  .s-add-profile__business-rates {
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }

  .s-add-profile__show-business-rates-btn {
    margin-top: 60px;
  }
}