.s-check-in {
  background-color: $light-gold;
}

.s-check-in__bg-wrap {
  background-color: $dark-blue-grey;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  color: #fff;
  text-align: center;
}

.s-check-in__text {
  font-weight: 300;
  padding: 0 25px;
  margin-bottom: 32px;
}

.s-check-in__first-phrase {
  display: inline-block;
  width: 100%;
}

.s-check-in__authorisation {
  color: $light-gold;
  text-decoration: underline;
}

.s-check-in__form {
  position: relative;
  margin-bottom: 32px;
  input {
    width: 100%;
    border-radius: 10px;
    padding: 20px;
    font-size: 18px;
    color: rgba(68, 68, 68, 1);
  }
  input::placeholder {
    color: rgba(68, 68, 68, .3);
    line-height: 1;
  }
}

.s-check-in__form-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 25%;
  height: 100%;
  span {
    display: none;
  }
}

.s-check-in__form-btn.btn--main::after {
  right: auto;
  transform: translate(-50%,-50%);
}

.s-check-in__contacts {
  display: flex;
  flex-direction: column;
}

.s-check-in__contact-item a {
  display: flex;
  align-items: center;
  color: $light-gold;
  font-weight: bold;
  text-transform: uppercase;
  transition: $default-transition;
  &:hover {
    color: $light-mango;
    transition: $default-transition;
    .s-check-in__contact-img-wrap svg {
      fill: $light-mango;
      transition: $default-transition;
    }
  }
}

.s-check-in__contact-img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 100%;
  margin-right: 15px;
}

.s-check-in__contact-img-wrap svg {
  fill: $light-gold;
  transition: $default-transition;
}

@media (min-width: $tablet-layout-width) {
  .s-check-in__text {
    font-size: 28px;
    margin-top: 60px;
    padding: 0;
  }

  .s-check-in__text--first {
    margin-bottom: 67px;
  }

  .s-check-in__text--second {
    margin-bottom: 43px;
  }

  .s-check-in__first-phrase {
    display: inline;
  }

  .s-check-in__form input {
    font-size: 24px;
  }

  .s-check-in__cnt-wrap {
    padding: 0 50px;
  }

  .s-check-in__form-btn {
    width: 15%;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }

  .s-check-in__contact-img-wrap {
    width: 67px;
    height: 67px;
  }

  .s-check-in__contact-img-wrap svg {
    width: 27px;
  }

  .s-check-in__contact-phone {
    height: 27px;
  }

  .s-check-in__contact-mail {
    height: 19px;
  }

  .s-check-in__contact-item a {
    font-size: 28px;
  }
}

@media (min-width: $desktop-layout-width) {
  .s-check-in__bg-wrap {
    padding-top: 105px;
    padding-bottom: 100px;
  }

  .s-check-in__form-btn span {
    display: inline;
    line-height: 0;
  }

  .s-check-in__form input {
    border-radius: 20px;
    padding: 30px;
  }

  .s-check-in__form {
    width: 85%;
    margin: 0 auto;
  }

  .s-check-in__form-btn {
    width: 30%;
    padding: 0;
    padding-left: 35px;
    text-align-last: left;
  }

  .s-check-in__form-btn.btn--main::after {
    right: 30px;
  }

  .s-check-in__contacts {
    flex-direction: row;
    justify-content: center;
  }

  .s-check-in__contact-item:first-child {
    margin-right: 110px;
  }

  .s-check-in__text--second {
    margin-top: 70px;
  }

  .s-check-in__contact-img-wrap {
    width: 90px;
    height: 90px;
    margin-right: 33px;
  }

  .s-check-in__contact-img-wrap svg {
    width: 36px;
  }

  .s-check-in__contact-phone {
    height: 36px;
  }

  .s-check-in__contact-mail {
    height: 26px;
  }

  .main-footer__logo {
    display: inline-block;
    width: 198px;
    height: 52px;
  }

}
