.s-my-plans {
  background-color: #edeff6;
}

.s-my-plans__inner {
  padding-top: 30px;
  padding-bottom: 20px;
  color: $dark-blue;
  background-color: #dfe3f0;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  h3 {
    margin-bottom: 22px;
    font-size: 24px;
    font-weight: 600;
  }
}

.s-my-plans__adding-step {
  padding: 25px;
  margin-bottom: 10px;
  color: $dark-blue;
  background-color: #fff;
  border-radius: 10px;
  h5 {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.1;
    span {
      display: block;
    }
  }
  p {
    margin-bottom: 22px;
    color: #444;
  }
  label {
    display: flex;
    font-weight: 500;
    line-height: 1;
    color: $dark-blue;
    text-transform: uppercase;
  }
}

.s-my-plans__countries {
  li {
    padding: 25px;
    margin-bottom: 10px;
    background-color: #fff;
    border-radius: 10px;
  }
  h5 {
    margin-bottom: 15px;
    font-size: 16px;
  }
}

.s-my-plans__form-change {
  input {
    font-family: inherit;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.4;
    text-transform: uppercase;
    color: $dark-blue;
    opacity: 0.7;
    border: none;
  }
  button {
    margin-top: 20px;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.3)
  }
}

.s-my-plans__fieldset-travel-date {
  input {
    min-width: 50px;
    width: 50px;
  }
  span {
    margin-right: 5px;
  }
}

.s-my-plans__title-span {
  margin-left: auto;
  color: rgba(29, 46, 91, 0.3);
}

.s-my-plans__search-companions, .s-my-plans__trip-duration {
  margin-top: 10px;
  margin-bottom: 20px;
  position: relative;
  .s-my-plans__btn-minus, .s-my-plans__btn-plus {
    position: absolute;
    padding: 10px;
    top: 50%;
    opacity: 0.3;
    transform: translateY(-50%);
    z-index: 1;
    svg {
      fill: #606161;
    }
  }
}

.s-my-plans__search-companions input, .s-my-plans__trip-duration input {
  box-sizing: inherit;
  width: 100%;
  padding: 10px;
  font-size: 15px;
  font-weight: 500;
  line-height: 0.9;
  text-align: center;
  color: inherit;
  border: solid 1px #cbced9;
}

.s-my-plans__approve-kids {
  margin-bottom: 25px;
  position: relative;
}

.s-my-plans__approve-kids .s-my-plans__approve-kids-label {
  display: inline-block;
  vertical-align: bottom;
  padding-left: 40px;
  font-size: 15px;
  text-transform: none;
  color: #444;
  opacity: 0.75;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 26px;
    height: 26px;
    border-radius: 4px;
    border: solid 1px #cbced9;
    background-image: none;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 9px 9px;
  }
}

.s-my-plans__approve-kids input:checked + label::before {
  background-image: url("../../../img/tick.svg");
}

.s-my-plans__calendar-months {
  display: flex;
  position: relative;
  border-top: 1px solid #d4d9eb;
  border-bottom: 1px solid #d4d9eb;
}

.s-my-plans__calendar-month {
  display: none;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  &--selected {
    display: block;
  }
}

.s-my-plans__calendar-month-prev, .s-my-plans__calendar-month-next {
  position: absolute;
  padding: 10px;
  top: 50%;
  opacity: 0.3;
  fill: #606161;
  transform: translateY(-50%);
  &:hover {
    cursor: pointer;
  }
}

.s-my-plans__calendar-month-prev, .s-my-plans__calendar-month-next {
  svg {
    width: 9px;
    height: 17px;
  }
}

.s-my-plans__calendar-month-prev {
  left: 0;
}

.s-my-plans__calendar-month-next {
  right: 0;
}

.s-my-plans__calendar-week-days {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.s-my-plans__calendar-week-day {
  width: 14.29%;
  height: 30px;
  text-transform: uppercase;
  font-size: 14px;
  text-align: center;
  line-height: 30px;
  color: #444444;
  &--weekend {
    color: #ff5c23;
  }
}

.s-my-plans__calendar-month-days {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin-bottom: 25px;
  border: 0.5px solid rgba($dark-blue, 0.25);
}

.s-my-plans__calendar-day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14.28%;
  height: 30px;
  color: rgba(68, 68, 68, 0.3);
  background-color: transparent;
  label {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 14px;
    border: 2px solid transparent;
    box-sizing: border-box;
    border: 0.5px solid rgba($dark-blue, 0.25);
    opacity: 0.35;
  }
  &--companion-exist {
    label {
      color: #444;
      background-color: #edeff6;
      opacity: 1;
    }
  }
  &--active {
    label {
      color: rgba(#edeff6, 0.9);
      background-color: $dark-blue;
      border-left: 1px solid rgba(#edeff6, 0.5);
    }
  }
  &--first-active {
    label {
      border: 2px solid #ff5c23;
    }
  }
}

.s-my-plans__calendar-day--first-active, .s-my-plans__calendar-day--last-active {
  span {
    display: none;
  }
}

.s-my-plans__calendar-day--first-month-day {
  span {
    display: none;
  }
}

.s-my-plans__dropdown-countries {
  display: none;
  position: absolute;
}

.s-my-plans__dropdown-countries--open {
  display: block;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 10;
  & > div {
    box-shadow: 0px 20px 50px 0 rgba(29, 46, 91, 0.1);
    border: solid 1px rgba(#cbced9, 0.5);
    border-radius: 10px;
    background-color: #fff;
  }
  button {
    width: 100%;
    padding: 10px;
    position: relative;
    margin-bottom: 0;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    text-align-last: left;
    line-height: 1.3;
    letter-spacing: 1.4px;
    border-radius: 4px;
    border: solid 1px #cbced9;
    background-color: $dark-blue;
    color: #fff;
    span {
      content: "";
      position: absolute;
      top: 50%;
      right: 13px;
      width: 9px;
      height: 9px;
      opacity: 0.5;
      background-color: transparent;
      background-image: url("../../../img/small-cross-white.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transform: translateY(-50%);
    }
  }
}

.s-my-plans__country-selection {
  .s-my-plans__dropdown-countries-letter {
    li {
      margin: 0;
    }
  }
}

.s-my-plans__country-selection .s-my-plans__dropdown-country-name {
  padding: 40px 20px 20px;
  li {
    margin-bottom: 7px;
  }
  label {
    text-transform: none;
    color: #444;
    font-weight: 400;
  }
}

.s-my-plans__dropdown-countries-letter {
  display: flex;
  flex-wrap: wrap;
  li {
    width: 20%;
    height: 42px;
    font-weight: 500;
    background-color: #fff;
    color: $dark-blue;
    text-transform: uppercase;
  }
  label {
    display: flex;
    width: 100%;
    height: 100%;
    font-size: 14px;
    justify-content: center;
    align-items: center;
    border: 0.5px solid #d4d9eb;
  }
}

.s-my-plans__country-selection li {
  display: flex;
  margin-bottom: 20px;
  input {
    width: 100%;
    height: 100%;
    border: none;
    padding: 10px;
    font-size: 14px;
    font-weight: 500;
    font-family: inherit;
    text-transform: uppercase;
    line-height: 1.3;
    letter-spacing: 1.4px;
    color: $dark-blue;
    border-radius: 4px;
    border: solid 1px #cbced9;
  }
}

.s-my-plans__country-select-wrap {
  width: 100%;
  position: relative;
}

.s-my-plans__select-country, .s-my-plans__add-country {
  width: 100%;
  padding: 10px;
  position: relative;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  text-align-last: left;
  line-height: 1.3;
  letter-spacing: 1.4px;
  border-radius: 4px;
  border: solid 1px #cbced9;
  color: $dark-blue;
}

.s-my-plans__add-country {
  background-color: #edeff6;
}

.s-my-plans__add-country + .s-my-plans__toggle-view-all-country {
  background: #edeff6;
  &::before {
    width: 14px;
    height: 14px;
    background-image: url("../img/qty-plus-mobile.svg");
  }
}

.s-my-plans__toggle-view-all-country {
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  width: 25%;
  background: linear-gradient(to left, #fff 75%, rgba(255,255,255, 0.5) 100%);
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 13px;
    width: 12px;
    height: 7px;
    opacity: 0.3;
    background-color: transparent;
    background-image: url("../../../img/dropdown-form.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transform: translateY(-50%);
  }
}

.s-my-plans__country-flag {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 7px;
  border-radius: 4px;
  border: solid 1px #cbced9;
  border-left: none;
  &::after {
    content: "";
    position: absolute;
    top: 43px;
    left: -3px;
    width: 5px;
    height: 10px;
    opacity: 0.4;
    background-image: url("../../../img/small-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}

.s-my-plans__country-delete {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 20px;
  height: 20px;
  background-image: url("../../../img/small-cross.svg");
  background-repeat: no-repeat;
  background-size: 9px 9px;
  background-position: center;
  background-color: #edeff6;
  border-radius: 100%;
}

.s-my-plans__entertainmet-item {
  padding-bottom: 45px;
  label {
    display: flex;
    flex-direction: column;
    position: relative;
    font-size: 18px;
    text-transform: none;
  }
  textarea {
    min-height: 220px;
    padding: 12px 15px;
    resize: none;
    font-size: 16px;
    border-radius: 4px;
    color: #444;
    border: solid 1px #cbced9;
    &::placeholder {
      opacity: 0.3;
    }
  }
}

.s-my-plans__entertainmet-title {
  padding-top: 5px;
  padding-left: 10px;
  margin-bottom: 15px;
  position: relative;
  span {
    width: 35%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to left, #fff 85%, rgba(255,255,255, 0.5) 100%);
  }
  img {
    position: absolute;
    right: 0;
  }
  &::before, &::after {
    content: "";
    position: absolute;
    top: 12px;
    background-color: $dark-blue;
  }
  &::before {
    width: 15px;
    height: 2px;
    left: -13px;
  }
  &::after {
    width: 6px;
    height: 6px;
    top: 10px;
    left: -3px;
    border-radius: 100%;
  }
}

.s-my-plans__entertainmet > li:not(:last-child) {
  position: relative;
  &::before {
    content: "";
    width: 2px;
    height: 100%;
    position: absolute;
    top: 12px;
    left: -13px;
    background-color: $dark-blue;
  }
}

.s-my-plans__steps-nav {
  display: none;
}

@media (min-width: $tablet-layout-width) {
  .s-my-plans__inner {
    padding: 50px;
    h3 {
      margin-bottom: 42px;
      font-size: 36px;
    }
  }
  
  .s-my-plans__countries {
    li {
      padding: 40px 35px 25px;
    }
    h5 {
      margin-bottom: 35px;
      font-size: 24px;
    }
  }

  .s-my-plans__form-change {
    display: flex;
    input {
      font-size: 20px;
    }
    button {
      margin: 0;
      margin-left: auto;
      font-size: 20px;
    }
  }

  .s-my-plans__fieldset-travel-date {
    input {
      width: 75px;
    }
  }

  .s-my-plans__toggle-steps {
    display: flex;
    flex-wrap: wrap;
  }
  
  .s-my-plans__entertainmet > li:not(:last-child)::before {
    display: none;
  }

  .s-my-plans__adding-step {
    padding: 42px 55px;
    border-radius: 30px;
    h5 {
      margin-bottom: 35px;
      font-size: 36px;
    }
    p {
      margin-bottom: 40px;
      font-size: 21px;
      line-height: 1.2;
    }
  }

  .s-my-plans__search-companions {
    margin-top: 25px;
  }


  .s-my-plans__search-companions, .s-my-plans__trip-duration {
    input {
      padding: 25px 20px;
      font-size: 20px;
    }
    svg {
      width: 20px;
      height: 20px;
    }
    .s-my-plans__btn-minus {
      left: 20px;
    }
    .s-my-plans__btn-plus {
      right: 20px;
    }
    label {
      font-size: 20px;
      padding-top: 25px;
      border-top: 1px solid rgba(#D7DAE7, 0.65);
    }
  }

  .s-my-plans__flex-wrapper {
    display: flex;
    flex-direction: column;
    & > div:nth-child(2) {
      margin-bottom: 60px;
      order: 1;
    }
  }

  .s-my-plans__approve-kids {
    margin-bottom: 35px;
    .s-my-plans__approve-kids-label {
      padding-left: 60px;
      margin-top: 10px;
      font-size: 20px;
      line-height: 0.9;
      letter-spacing: 2px;
      vertical-align: middle;
      text-transform: uppercase;
      color: $dark-blue;
      &::before {
        width: 36px;
        height: 36px;
        background-size: 12px 12px;
      }
    }
  }

  .s-my-plans__calendar-day {
    height: 70px;
    label {
      padding: 0;
      font-size: 20px;
    }
  }

  .s-my-plans__calendar-week-days {
    font-size: 18px;
    margin-top: 30px;
  }

  .s-my-plans__calendar-week-day {
    font-size: inherit;
  }

  .s-my-plans__calendar-month {
    padding: 25px;
    font-size: 36px;
  }

  .s-my-plans__calendar-month-prev, .s-my-plans__calendar-month-next {
    padding: 25px;
    svg {
      width: 15px;
      height: 28px;
    }
  }
  
  .s-my-plans__country-select-wrap {
    width: 70%;
  }

  .s-my-plans__country-select-wrap {
    .s-my-plans__select-country {
      padding: 24px 27px;
      font-size: 20px;
      line-height: 0.9;
      letter-spacing: 2px;
    }
  }

  .s-my-plans__country-selection li input,
  .s-my-plans__select-country,
  .s-my-plans__add-country
  {
    padding: 24px 27px;
    font-size: 20px;
    line-height: 0.9;
    letter-spacing: 2px;
  }

  .s-my-plans__toggle-view-all-country {
    width: 20%;
    &::before {
      width: 17px;
      height: 9px;
      right: 27px;
    }
  }

  .s-my-plans__country-flag {
    padding: 10px 20px;
    margin-left: 36px;
    border: none;
    img {
      width: 70px;
      height: 47px;
    }
    &::after {
      display: none;
    }
  }

  .s-my-plans__country-delete {
    top: 50%;
    right: -20px;
    margin-top: -10px;
    background-size: 20px 20px;
    background-color: transparent;
    opacity: 0.35;
  }

  .s-my-plans__country-selection {
    padding-top: 40px;
    margin-bottom: 40px;
    position: relative;
    border-top: 1px solid #d6d9e6;
  }

  .s-my-plans__country-select-wrap {
    &::before {
      content: "";
      width: 16px;
      height: 16px;
      margin-top: -8px;
      position: absolute;
      top: 50%;
      right: -36px;
      z-index: 1;
      background-color: $dark-blue;
      border-radius: 100%;
    }
    &::after {
      content: "";
      width: 1px;
      position: absolute;
      top: -40px;
      bottom: -20px;
      right: -28px;
      z-index: 0;
      background-color: #d6d9e6;
    }
  }

  .s-my-plans__country-select-wrap--adding {
    &::before {
      background-color: #edeff6;
      z-index: 1;
    }
  }

  .s-my-plans__country-select-wrap--selection {
    &::before {
      border: 1px solid #cbced9;
      background-color: #fff;
      z-index: 1;
    }
  }

  .s-my-plans__entertainmet-title {
    padding: 0;
    margin-bottom: 37px;
    font-size: 30px;
    font-weight: bold;
    &::before, &::after {
      display: none;
    }
  }

  .s-my-plans__entertainmet .s-my-plans__entertainmet-item {
    padding-bottom: 60px;
    label {
      padding-top: 18px;
      border: none;
    }
  }

  .s-my-plans__entertainmet-title {
    span {
      display: block;
      width: auto;
      top: -7px;
      img {
        width: 70px;
        height: 47px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 110px;
        margin-top: -3px;
        width: 16px;
        height: 16px;
        background-color: $dark-blue;
        border-radius: 100%;
      }
    }
  }

  .s-my-plans__entertainmet-item textarea {
    font-size: 20px;
    line-height: 1.2;
  }

  .s-my-plans__adding-step {
    .s-my-plans__dropdown-countries--open {
      & > div {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        position: absolute;
        left: 0;
        width: 144%;
        border: none;
      }
      button {
        width: 100%;
        padding: 25px;
        margin: 0;
        font-size: 20px;
        font-weight: 400;
        span {
          width: 17px;
          height: 17px;
          right: 25px;
        }
      }
    }
  }

  .s-my-plans__country-selection .s-my-plans__dropdown-countries-letter {
    width: 58%;
    padding: 34px 0 96px 27px;
    li {
      height: 70px;
      label {
        padding: 0;
        font-size: 20px;
        font-weight: 500;
      }
    }
  }

  .s-my-plans__country-selection .s-my-plans__dropdown-country-name {
    padding: 37px 25px;
    padding-top: 0;
    margin-top: 37px;
    padding-right: 35px;
    border-left: 1px solid rgba(#cbced9, 0.4);
    li label {
      padding: 0;
      border: none;
    }
  }

}

@media (min-width: $desktop-layout-width) {
  .s-my-plans__countries {
    li {
      display: flex;
      align-items: center;
      padding: 37px 42px;
      margin-bottom: 15px;
    }
    h5 {
      margin-right: 50px;
      margin-bottom: 0;
    }
  }
  .s-my-plans__form-change {
    justify-content: space-between;
    min-width: 50%;
    margin-left: auto;
  }

  .s-my-plans__fieldset-companions-count {
    input {
      width: 140px;
      margin-right: 20px;
    }
  }

  .s-my-plans__adding-step {
    padding: 100px 160px;
    h5 {
      margin-bottom: 25px;
      span {
        display: inline-block;
      }
    }
    p {
      margin: 0;
    }
  }

  .s-my-plans__adding-step-wrap {
    display: flex;
    margin-bottom: 55px;
    & > div {
      flex-basis: 60%;
    }
  }

  .s-my-plans__steps-nav {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 0.69;
    a {
      display: block;
      padding: 10px 35px;
      letter-spacing: 2px;
      color: #000;
      opacity: 0.5;
    }
  }

  .s-my-plans__steps-nav-item {
    margin: 3px 0;
    position: relative;
    &--active {
      a {
        opacity: 1;
        color: $dark-blue;
      }
    }
    span {
      width: 6px;
      height: 5px;
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -3px;
      background-color: #808285;
      opacity: 0.3;
    }
    &--active {
      span {
        background-color: $dark-blue;
        opacity: 1;
      }
    }
  }

  .s-my-plans__countries {
    margin: 0 60px;
  }

  .s-add-plan-inner__caption-wrap {
    margin: 0 60px;
  }

  .s-my-plans__inner h3 {
    margin-left: 60px;
  }

  .s-add-plan-inner__small-nav-btns {
    display: none;
  }

  .s-my-plans__flex-wrapper {
    & > div {
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      position: relative;
      &:not(.s-my-plans__approve-kids) {
        width: 45%;
      }
    }
  }

  .s-my-plans__flex-wrapper > div:nth-child(2) {
    margin-bottom: 32px;
  }

  .s-my-plans__flex-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    border-top: 1px solid rgba(215, 218, 231, 0.65);
    padding-top: 30px;
  }

  .s-my-plans__approve-kids {
    margin-bottom: 0;
    margin-left: auto;
    .s-my-plans__approve-kids-label {
      margin: 0;
      position: relative;
      &::before {
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .s-my-plans__search-companions, .s-my-plans__trip-duration {
    margin: 0 65px 0 auto;
    width: 40%;
    label {
      padding-top: 0;
      border: none;
    }
  }

  .s-my-plans__title-span {
    position: absolute;
    top: 50%;
    right: 0;
    color: rgba(29, 46, 91, 1);
    transform: translateY(-50%)
  }

  .s-my-plans__calendar-month {
    padding: 29px 22px 22px 22px;
  }

  .s-my-plans__calendar-month-prev, .s-my-plans__calendar-month-next {
    padding: 29px 22px 22px 22px;
    svg {
      width: 18px;
      height: 34px;
    }
  }

  .s-my-plans__calendar-week-days {
    margin-top: 10px;
  }

  .s-my-plans__calendar-week-day {
    padding-left: 15px;
    margin-bottom: 7px;
    text-align: left;
  }

  .s-my-plans__calendar-month-days {
    margin-bottom: 45px;
  }

  .s-my-plans__calendar-day {
    min-height: 72px;
  }

  .s-my-plans__calendar-day label {
    padding: 10px 12px;
    align-items: stretch;
    justify-content: flex-start;
  }

  .s-my-plans__search-companions, .s-my-plans__trip-duration {
    label {
      font-size: 18px;
      font-weight: normal;
      letter-spacing: 1.8px;
    }
  }

  .s-my-plans__calendar-day--first-active, .s-my-plans__calendar-day--last-active {
    span {
      display: block;
      text-transform: none;
      margin-top: 8px;
    }
    label {
      display: inline-block;
      width: 100%;
    }
  }

  .s-my-plans__calendar-day--first-month-day {
    span {
      text-transform: none;
      margin-left: 8px;
    }
  }

  .s-my-plans__country-select-wrap {
    width: 76%;
  }

  .s-my-plans__country-flag {
    position: static;
  }

  .s-my-plans__country-selection li {
    position: relative;
  }

  .s-my-plans__country-delete {
    right: 0;
  }

  .s-my-plans__toggle-steps .btn--add-plane-step-prev {
    width: auto;
    padding-right: 5px;
  }

  .s-my-plans__entertainmet {
    padding-top: 80px;
    border-top: 1px solid #d6d9e6;
    .s-my-plans__entertainmet-item {
      width: 90%;
      padding-bottom: 60px;
      textarea {
        width: 80%;
        min-height: 142px;
      }
    }
  }

  .s-my-plans__entertainmet-item:not(:last-child) {
    &::after{
      content: "";
      width: 3px;
      height: 100%;
      position: absolute;
      right: 116px;
      top: 27px;
      background-color: $dark-blue;
    }
  }

  .s-my-plans__adding-step .s-my-plans__dropdown-countries--open > div {
    width: 100%;
    li {
      width: 15%;
      margin-bottom: 15px;
      margin-right: 15px;
      border-width: 1px;
    }
  }

  .s-my-plans__country-selection .s-my-plans__dropdown-countries-letter {
    padding-bottom: 35px;
  }

  .s-my-plans__country-selection .s-my-plans__dropdown-country-name {
    padding-right: 90px;
  }

}

// :hover, :focus, :active etc.
.s-my-plans__form-change {
  button {
    @include transition(color);
    &:hover, &:focus {
      color: rgba(0,0,0,0.6);
      @include transition(color);
    }
    &:active {
      color: rgba(0,0,0,0.1);
      @include transition(color);
    }
  }
}

.s-my-plans__search-companions, .s-my-plans__trip-duration {
  @include transition(border-color);
  input:focus {
    outline: none;
    border-color: #161c35;
    @include transition(border-color);
  }
  &:hover input{
    border-color: #959bb2;
    @include transition(border-color);
  }
  button {
    &:hover, &:focus {
      outline: none;
      opacity: 0.5;
    }
    &:active {
      opacity: 0.1;
    }
  }
}

.s-my-plans__approve-kids {
  input {
    &:focus + label {
      opacity: 1;
      @include transition(opacity);
      &:before {
        border-color: #959bb2;
        @include transition(border-color);
      }
    }
    &:active + label:before{
      background-color: #e2e5f1;
      @include transition(background-color);
    }
  }
  label:hover {
    @include transition(border-color);
    &:before {
      border-color: #959bb2;
      @include transition(border-color);
    }
  }
}

.s-my-plans__calendar-month-prev, .s-my-plans__calendar-month-next {
  @include transition(opacity);
  &:hover, &:focus {
    outline: none;
    opacity: 0.5;
    @include transition(opacity);
  }
  &:active {
    opacity: 0.1;
    @include transition(opacity);
  }
}

.s-my-plans__calendar-day {
  input {
    @include transition(opacity);
    &:focus + label {
      opacity: 0.2;
      @include transition(opacity);
    }
    &:active + label {
      opacity: 0.2;
      @include transition(opacity);
    }
  }
  label:hover {
    cursor: not-allowed;
  }
}

.s-my-plans__calendar-day--companion-exist {
  input {
    &:focus + label {
      opacity: 1;
      border: solid 2px #ff5c23;
      background-color: $dark-blue;
      color: #fff;
    }
    &:active + label {
      opacity: 1;
      background-color: $dark-text-color;
    }
  }
}

.s-my-plans__calendar-day--companion-exist {
  label {
    @include transition(background-color);
    @include transition(border);
    @include transition(color);
    &:hover {
      border: solid 2px #ff5c23;
      background-color: $dark-blue;
      color: #fff;
      cursor: pointer;
      @include transition(background-color);
      @include transition(border);
      @include transition(color);
    }
    &:active {
      background-color: $dark-text-color;
      @include transition(background-color);
    }
  }
}

.s-my-plans__steps-nav-item {
  a {
    @include transition(opacity);
    @include transition(color);
    &:hover, &:focus {
      outline: none;
      opacity: 1;
      color: #1d2e5b;
      @include transition(opacity);
      @include transition(color);
      & + span {
        background-color: #1d2e5b;
        opacity: 1;
        @include transition(opacity);
        @include transition(background-color);
      }
    }
    &:active {
      @include transition(opacity);
      opacity: 0.5;
      & + span {
        opacity: 0.5;
        @include transition(opacity);
      }
    }
  }
}

.s-my-plans__add-country, .s-my-plans__country-selection li input, .s-my-plans__select-country {
  @include transition(border-color);
  &:hover, &:focus, &:active {
    outline: none;
    border-color: #959bb2;
    @include transition(border-color);
  }
}

.s-my-plans__toggle-view-all-country {
  @include transition(opacity);
  &:hover, &:focus, &:active {
    outline: none;
    &::before {
      opacity: 0.75;
      @include transition(opacity);
    }
  }
}

.s-my-plans__country-delete {
  @include transition(opacity);
  &:hover, &:focus {
    outline: none;
    opacity: 1;
    @include transition(opacity);
  }
  &:active {
    opacity: 0.25;
    @include transition(opacity);
  }
}

.s-my-plans__dropdown-countries--open button {
  &:focus {
    outline: none;
  }
  span {
    @include transition(opacity);
    &:hover, &:focus {
      outline: none;
      opacity: 1;
      @include transition(opacity);
    }
    &:active {
      outline: none;
      opacity: 0.25;
      @include transition(opacity);
    }
  }
}

.s-my-plans__country-selection .s-my-plans__dropdown-countries-letter li {
  input {
    @include transition(background-color);
    @include transition(color);
    &:checked + label {
      background-color: $dark-blue;
      color: #fff;
      @include transition(background-color);
      @include transition(color);
    }
  }
  label {
    @include transition(border-color);
    &:hover {
      cursor: pointer;
      border-color: #959bb2;
      @include transition(border-color);
    }
  }
}

.s-my-plans__dropdown-country-name li {
  input {
    @include transition(color);
    &:focus + label {
      outline: none;
      color: $dark-blue;
      font-weight: bold;
      @include transition(color);
    }
  }
}

.s-my-plans__entertainmet-item textarea {
  position: relative;
  @include transition(border-color);
  &:hover, &:focus {
    outline: none;
    border-color: #959bb2;
    @include transition(border-color);
  }
  &:active {
    border-color: $dark-text-color;
    @include transition(border-color);
  }
  &:invalid {
    border-color: #f47e7e;
    @include transition(border-color);
    & + span {
      display: inline-block;
    }
  }
}
