.s-filter {
  background-color: $light-grey;
}

.s-filter__wrap-bg {
  background-color: #e2e5f2;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.s-filter__wrap-ctn {
  position: relative;
  padding-top: 20px;
  padding-bottom: 27px;
}

.s-filter__countries-letters-list {
  display: flex;
  flex-wrap: wrap;
}

.s-filter__title {
  position: relative;
  text-transform: uppercase;
  font-weight: bold;
  padding-left: 27px;
  margin-bottom: 10px;
  font-size: 16px;
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 15px;
    height: 10px;
    background: transparent url("../../../img/filter.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.s-filter__close-btn {
  svg {
    width: 12px;
    height: 12px;
    fill: $dark-blue-grey;
  }
}

.s-filter__close-btn--small {
  position: absolute;
  display: flex;
  align-items: center;
  top: 14px;
  right: -5px;
  padding: 10px;
  span {
    display: none;
  }
}

.s-filter__close-btn--big {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  color: $dark-text-color;
  font-weight: bold;
  text-transform: uppercase;
  background-color: #d6daed;
  border-radius: 20px;
  padding: 12px;
  span {
    margin-left: 18px;
  }
}

.s-filter__ctn {
  padding-left: 27px;
}

.s-filter__continents-list {
  padding: 23px 0;
  text-transform: uppercase;
}

.s-filter__continent {
  color: $dark-blue-grey;
  font-weight: bold;
  margin: 2px 0;
  label:hover {
    cursor: pointer;
  }
  label {
    opacity: .3;
  }
  input:checked + label {
    opacity: 1;
  }
  input:focus + label {
    border: 1px solid $light-mango;
  }
}

.s-filter__countries-letters-list {
  margin-bottom: 50px;
  margin-right: 20px;
  border: 0.5px solid #cbced9;
}

.s-filter__country-letter {
  display: flex;
  width: 20%;
  height: 41px;
  background-color: #edeff6;
  color: $dark-blue;
  font-weight: 500;
  border: 0.5px solid #cbced9;
  label {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    background-color: transparent;
    &:hover {
      cursor: pointer;
    }
  }
  input:checked + label {
    background-color: $dark-blue;
    color: #fff;
  }
  input:focus + label {
    outline: 1px solid $light-mango;
  }
}

.s-filter__countries-result-list {
  margin-bottom: 22px;
  li {
    margin: 2px 0;
  }
  a {
    color: #444;
  }
}

.s-filter__country-by-letter {
  display: none;
}

@media (min-width: $tablet-layout-width) {
  .s-filter__wrap-bg {
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
  }
  
  .s-filter__ctn {
    padding: 0 30px;
  }

  .s-filter__wrap-ctn {
    padding-top: 55px;
    padding-bottom: 75px;
  }

  .s-filter__title {
    display: none;
  }

  .s-filter__close-btn {
    svg {
      width: 19px;
      height: 19px;
    }
  }

  .s-filter__close-btn--small {
    top: 60px;
    right: 50px;
    span {
      display: inline;
      font-size: 20px;
      font-weight: bold;
      line-height: 1;
      text-transform: uppercase;
      color: $dark-blue-grey;
      padding-right: 60px;
    }
  }

  .s-filter__close-btn--big {
    margin: 75px auto 0px;
    width: 100%;
    padding: 42px;
    span {
      font-size: 20px;
      font-weight: bold;
    }
  }

  .s-filter__continents-list {
    padding: 0;
    margin-bottom: 65px;
    display: flex;
  }

  .s-filter__continent {
    margin: 0;
    margin-right: 25px;
    font-size: 20px;
  }

  .s-filter__countries {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .s-filter__countries-letters-list {
    flex-basis: 400px;
    justify-content: space-between;
    margin-left: -15px;
    margin-bottom: 0;
    border: none;
  }

  .s-filter__country-letter {
    width: 70px;
    height: 70px;
    background-color: transparent;
    font-weight: bold;
    border: none;
    label {
      opacity: .2;
      font-size: 60px;
    }
    input:checked + label {
      opacity: 1;
      color: $dark-blue-grey;
      background-color: transparent;
    }
  }

  .s-filter__countries-result-list {
    width: 170px;
  }

  .s-filter__countries-result-list a {
    font-size: 20px;
  }

}

@media (min-width: $desktop-layout-width) {
  .s-filter__wrap-bg {
    background-color: transparent;
  }

  .s-filter__ctn {
    padding: 0 35px;
  }
  
  .s-filter__title {
    display: inline-block;
    margin-left: 35px;
    font-size: 20px;
    padding-left: 45px;
    &::before {
      width: 23px;
      height: 15px;
    }
  }

  .s-filter__continents-list {
    position: relative;
    top: -37px;
    left: 320px;
    max-width: 650px;
    flex-wrap: wrap;
    margin-bottom: 15px;
  }

  .s-filter__countries-result-list {
    display: none;
  }
  
  .s-filter__countries-letters-list {
    flex-basis: 100%;
    margin-left: 0;
  }

  .s-filter__country-letter {
    width: 20%;
    height: auto;
    flex-direction: column;
    label {
      justify-content: flex-start;
    }
  }

  .s-filter__country-by-letter {
    display: block;
    margin-top: 40px;
    margin-bottom: 50px;
    a {
      font-size: 20px;
      font-weight: normal;
      line-height: 1.5;
      color: #444;
    }
  }

  .s-filter__country-letter label {
    opacity: 1;
  }

}
