.companion-card {
  display: flex;
  flex-direction: column;
}

.companion-card__user-foto img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.companion-card__user-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #fff;
  color: $dark-blue;
  padding: 0 25px 22px 25px;
  flex-grow: 1;
}

.companion-card__preferred-transport {
  margin-top: auto;
}

.companion-card__flags {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.companion-card__flags svg {
  position: relative;
  top: -12px;
  margin: 0px 10px 5px;
}

.companion-card__username {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  line-height: 1;
  margin-bottom: 15px;
  .companion-card__status {
    display: inline-block;
    width: 5px;
    height: 5px;
    margin-right: 10px;
    border-radius: 50px;
  }
  .companion-card__status--online {
    background-color: $green-apple;
  }
  .companion-card__status--offline {
    background-color: $peach;
  }
}

.companion-card__hobbies-tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 15px;
  color: #444;
  line-height: 1.13;
}

.companion-card__hobbies-tag {
  margin: 0 3px;
}

.companion-card__transport-list {
  display: flex;
}

.companion-card__transport-item {
  fill: rgba(68, 68, 68, 0.2);
  margin: 0 8px;
  &--selected {
    fill: $dark-blue;
  }
}