.main-header-inner-pages {
  background-color: #e2e5f2;
  .main-header__btns--close-menu {
    top: auto;
  }
}

.main-header-inner-pages__bg-wrap {
  background-image: none;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  h1 {
    position: relative;
    top: 2.5px;
    display: inline-block;
    width: auto;
    margin: 0;
    padding-bottom: 10px;
    padding-top: 27px;
    text-align: left;
    border-bottom: 5px solid $mango;
    border-radius: 2.4px;
  }
  .main-header__menu--close .main-header__btns {
    display: none;
  }
}

.main-header__nav-btn {
  display: none;
}

@media (min-width: $tablet-layout-width) {
  .main-header-inner-pages {
    .main-header-inner-pages__bg-wrap {
      background-color: $dark-text-color;
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 50px;
    }
    .main-header__menu--close .main-header__btns {
      display: flex;
    }
  }

  .main-header__nav-btn {
    display: block;
    position: relative;
    top: 130px;
    color: #fff;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 1.2;
    font-weight: 500;
    float: right;
    padding-right: 60px;
    margin-right: 50px;
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 32px;
      height: 16px;
      background-image: url("../../../img/return.svg");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
  }
  
  .main-header-inner-pages__bg-wrap h1 {
    padding-top: 100px;
    padding-bottom: 65px;
    line-height: 1.15;
    margin-left: 50px;
  }
}

@media (min-width: $desktop-layout-width) {
  .main-header-inner-pages {
    .main-header__btns--close-menu {
      top: 535px;
    }
    h1 {
      padding-top: 0;
      padding-left: 0;
      margin-left: 60px;
    }
    .main-header__menu {
      padding-bottom: 75px;
    }
    .main-header__menu--hidden {
      padding-right: 60px;
    }
    .main-header__nav-btn {
      display: none;
    }
  }

}
