.transport-list {
  display: flex;
  flex-wrap: wrap;
}

.transport-list__item {
  position: relative;
  background-color: $light-grey;
  width: 33px;
  height: 32px;
  border-radius: 100%;
  input:checked + label {
    & > svg {
      opacity: 1;
    }
  }
  input {
    &:focus + label {
      box-shadow: 0px 2px 10px 0 rgba(0, 0, 0, 0.08);
      border: 1px solid rgba(0, 0, 0, 0.5);
    }
    &:active + label {
      background-color: #e2e5f1;
      svg {
        opacity: 0.3;
      }
    }
  }
  label {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    padding: 0;
    &::before {
      display: none;
    }
    svg {
      opacity: 0.15;
    }
  }
  &:hover {
    box-shadow: 0px 2px 10px 0 rgba(0, 0, 0, 0.08);
  }
}

@media (min-width: $tablet-layout-width) {
  .transport-list {
    margin: 0;
  }

  .transport-list__item {
    width: 43px;
    height: 43px;
    margin-right: 12px;
    margin-bottom: 0;
    label {
      padding: 0;
    }
  }
  
}

@media (min-width: $desktop-layout-width) {
  .transport-list__item {
    margin-right: 0
  }

}
