.s-main-user-info {
  background-color: $light-grey;
  padding: 25px 0;
}

.s-main-user-info__setting {
  display: flex;
  justify-content: space-around;
  align-items: start;
  align-content: start;
  flex-wrap: wrap;
  button, .s-main-user-info__hobby, .s-main-user-info__transport {
    flex-basis: 100%;
  } 
  button {
    padding: 12px;
    margin-top: 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
    background-color: #dfe3f0;
    border-radius: 20px;
  }
}

.s-main-user-info__foto img {
  border-radius: 100%;
}

.s-main-user-info__traveler-level {
  svg {
    width: 100px;
    height: 100px;
  }
}

.s-main-user-info__num {
  font-size: 38px;
  top: 20%;
  &::after {
    background-color: #cbced9;
    height: 2px;
  }
}

.s-main-user-info__text {
  font-size: 18px;
  line-height: 1.25;
  top: 60%;
}

.s-main-user-info__hobby-wrap {
  margin-bottom: 25px;
  position: relative;
  border: solid 1px #cbced9;
  border-radius: 10px;
  textarea {
    width: 100%;
    height: 100%;
    padding: 20px 25px;
    background-color: transparent;
    border: none;
    resize: none;
  }
}

.s-main-user-info__transport-wrap {
  padding: 20px 25px;
  position: relative;
  border: solid 1px #cbced9;
  border-radius: 10px;
  .transport-list {
    justify-content: center;
  }
}

.s-main-user-info__hobby-fake-legend, .s-main-user-info__transport-fake-legend {
  padding: 5px 15px;
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  color: $dark-blue;
  background-color: #edeff6;
}

@media (min-width: $tablet-layout-width) {
  .s-main-user-info {
    padding: 60px 50px;
  }
  .s-main-user-info__transport {
    order: -1;
  }
  .s-main-user-info__setting {
    justify-content: space-between;
    .s-main-user-info__transport {
      flex-basis: auto;
    }
    button {
      margin-top: 30px;
      margin-bottom: 50px;
      padding: 22px;
      font-size: 20px;
      font-weight: 500;
      letter-spacing: 2px;
    }
  }
  .s-main-user-info__traveler-level {
    order: -2;
  }
  .s-main-user-info__transport-wrap {
    padding: 25px 55px;
  }
  .s-main-user-info__hobby-fake-legend, .s-main-user-info__transport-fake-legend {
    top: auto;
    bottom: -18px;
    font-size: 20px;
    letter-spacing: 2px;
  }
  .s-main-user-info__foto img {
    border-radius: 6px;
  }
  .s-main-user-info__hobby-wrap {
    textarea {
      font-size: 20px;
      padding: 35px;
    }
  }

}

@media (min-width: $desktop-layout-width) {
  .s-main-user-info__setting {
    margin: 0 50px;
    position: relative;
  }

  .s-main-user-info__traveler-level, .s-main-user-info__foto, .s-main-user-info__setting button {
    position: absolute;
  }

  .s-main-user-info__traveler-level {
    top: -210px;
    right: 300px;
    span {
      color: #fff;
    }
  }

  .s-main-user-info__foto {
    right: 80px;
    top: -250px;
    img {
      width: 190px;
      height: 190px;
    }
  }

  .s-main-user-info__setting button {
    width: 190px;
    right: 80px;
    top: -90px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    span {
      display: none;
    }
  }

  .s-main-user-info__transport {
    order: 1;
    margin-top: 30px;
  }

  .s-main-user-info__hobby-wrap, .s-main-user-info__transport-wrap{
    height: 100px;
  }

  .s-main-user-info__transport-wrap{
    padding: 25px 60px;
  }

  .s-main-user-info__setting {
    .s-main-user-info__hobby {
      flex-basis: 60%;
      margin-top: 30px;
      order: 0;
    }
  }

  .s-main-user-info__hobby-fake-legend {
    left: 22px;
    transform: translateX(0);
  }
}

// :hover, :focus, :active etc.
.s-main-user-info__hobby-wrap {
  @include transition(border-color);
  &:hover {
    border-color:#aeb4cb;
    @include transition(border-color);
  }
  textarea {
    @include transition(border-color);
    &:focus {
      outline: none;
      border: solid 1px $dark-text-color;
      border-radius: 10px;
      box-sizing: border-box;
      @include transition(border-color);
    }
  }
}

.s-main-user-info__setting button {
  @include transition(background-color, color);
  &:hover, &:focus {
    background-color: #d1d7e9;
    @include transition(background-color);
  }
  &:active {
    color: rgba($dark-blue, 0.3);
    @include transition(color);
  }
}