.s-companions {
  background-color: $light-grey;
  padding-top: 27px;
  padding-bottom: 46px;
}

.s-companions__filter-wrap {
  background-color: $light-gold;
  color: $dark-blue;
  border-radius: 20px;
  padding: 23px 20px;
  margin-bottom: 28px;
  h3 {
    line-height: 1;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 20px;
  }
}

.s-companions-form {
  fieldset {
    margin-left: 5px;
    margin-right: 5px;
  }
  .s-companions-form__list, .s-companions-form__level-nums, .s-companions-form__range {
    margin-bottom: 20px;
  }
}

.s-companions-form__fake-legend {
  position: relative;
  width: 100%;
  font-weight: bold;
  text-transform: uppercase;
  padding: 12px 0;
  border-top: 1px solid #f3c72b;
  text-align: left;
  color: $dark-blue;
  &::after {
    content: "";
    position: absolute;
    width: 8px;
    height: 4px;
    top: 50%;
    right: 0;
    transform: translateY(-50%) rotate(180deg);
    background-image: url("../../../img/dropdown.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.s-companions-form__show-result-btn {
  display: block;
  width: 100%;
  border-radius: 20px;
  background-color: #fff;
  font-size: 17px;
  font-weight: bold;
  color: $dark-text-color;
  text-transform: uppercase;
  padding: 12px;
}

.s-companions-form__fieldset-close {
  .s-companions-form__fake-legend::after {
    transform: translateY(-50%) rotate(0deg);
  }
  ul {
    display: none;
  }
}

.s-companions-form__item {
  & > label {
    position: relative;
    padding-left: 27px;
    &::before {
      content: "";
      position: absolute;
      width: 18px;
      height: 18px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: #fff;
      border-radius: 4px;
    }
  }
  & > input:checked + label {
    &::before {
      background-image: url("../../../img/tick.svg");
      background-repeat: no-repeat;
      background-size: 9px 9px;
      background-position: center;
    }
  }
  & > input:focus + label {
    border: 1px solid $dark-blue;
  }
}

.s-companions-form__level ul {
  display: flex;
  flex-wrap: wrap;
}

.s-companions-form__level-nums {
  position: relative;
  input {
    border: solid 1px #cbced9;
    background-color: #ffd74b;
    max-width: 100%;
    font-size: 15px;
    font-weight: 500;
    line-height: 1;
    text-align: center;
    padding: 12px;
    color: $dark-blue;
    background-color: #fff;
  }
  li {
    width: calc(50% - 1px);
  }
  li:first-child input {
    border-right: none;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
  li:last-child input {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  &::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 2px;
    background-color: #cbced9;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.s-companions-form__level-value {
  input {
    width: 100%;
  }
}

.s-companions-form__range {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: $dark-text-color;
  border-radius: 1px;
  .s-companions-form__range-value {
    width: 11px;
    height: 10px;
    position: absolute;
    top: 50%;
    background-color: $dark-blue;
    transform: translateY(-50%);
    border-radius: 100%;
    &--min {
      left: 0;
    }
    &--max {
      right: 0;
    }
  }
}

.s-companions__traveler-card {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-top: 15px;
  padding-bottom: 22px;
  margin-bottom: 10px;
  background-color: #fff;
  border-radius: 20px;
}

.s-companions__traveler-img {
  position: absolute;
  margin: 0 20px;
  img {
    border-radius: 100%;
  }
}

.s-companions__traveler-name {
  margin-left: 100px;
  h4 {
    position: relative;
    display: block;
    font-size: 20px;
    font-weight: bold;
    color: $dark-blue;
    line-height: 1;
    padding-right: 30px;
    margin-right: 20px;
  }
}

.s-companions__traveler-status {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  &--offline {
    background-color: #f97676;
  }
  &--online {
    background-color: #81e31e;
  }
}

.s-companions__tags {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
  li {
    margin-right: 5px;
    line-height: 1.2;
    font-size: 15px;
    color: #444444;
  }
}

.s-companions__transport-and-level {
  display: flex;
}

.s-companions__traveler-level {
  margin-right: 20px;
}

.s-companions__traveler-level-title {
  display: block;
  text-align: center;
  margin-bottom: 5px;
}

.s-companions__transport-wrap {
  flex-grow: 1;
}

.s-companions__transport-list {
  display: flex;
  li {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $light-grey;
    width: 33px;
    height: 32px;
    border-radius: 100%;
  }
}



.s-companions__transport-item {
  svg {
    fill: $dark-blue;
    opacity: 0.15;
  }
}

.s-companions__transport-item--selected {
  svg {
    opacity: 1;
  }
}

.s-companions__tags, .s-companions__desired-countries .s-companions__desired-countries-list,
.s-companions__transport-list {
  margin-right: 25px;
  margin-left: 25px;
}

.s-companions__desired-countries {
  margin-top: 20px;
  margin-bottom: 20px;
}

.s-companions__desired-countries-title, .s-companions__transport-title {
  position: relative;
  display: block;
  margin-bottom: 12px;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: $light-grey;
    left: 0;
    top: 50%;
    z-index: 1;
  }
  small {
    position: relative;
    bottom: 1px;
    margin-left: 18px;
    padding: 0 7px;
    z-index: 2;
    background-color: #fff;
    font-size: 15px;
    line-height: 1.2;
  }
}

.s-companions__traveler-level-ctn {
  svg {
    width: 45px;
    height: 45px;
  }
  circle {
    stroke-width: 4px;
  }
}

.s-companions__traveler__num {
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  &::after {
    display: none;
  }
}

.s-companions__traveler__text {
  display: none;
}

.s-companions__traveler-likes-and-btn {
  display: flex;
  align-items: flex-start;
}

.s-companions__traveler-card-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 20px 0;
  width: 100%;
  height: 50px;
}

.s-companions__traveler-likes {
  position: absolute;
  top: 55px;
  left: 65px;
  display: flex;
  align-items: center;
  span {
    font-size: 14px;
    font-weight: bold;
    line-height: 1.5;
    text-transform: uppercase;
  }
}

.s-companions__traveler-like {
  width: 31px;
  height: 31px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 3px;
  &--on {
    background-color: #f9eaea;
  }
  &--off {
    background-color: $light-grey;
  }
}

.s-companions__traveler-like--on {
  svg {
    fill: #f02323;
  }
}

.s-companions__traveler-like--off {
  svg {
    fill: transparent;
    stroke: $dark-blue-grey;
    stroke-width: 2px;
    stroke-opacity: 0.3;
  }
}

.s-companions__travelers-btn-view-more {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  text-transform: uppercase;
  color: $dark-blue;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  span {
    display: inline-block;
    width: 18px;
    height: 18px;
    background-color: transparent;
    background-image: url("../../../img/show-more-plus.svg");
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 15px;
  }
}

.s-companions__page-navigation {
  background-color: #fff;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 30px;
}

.s-companions__page-navigation-nums {
  display: none;
  li {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    
  }
  button {
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    line-height: 50px;
    text-align: center;
  }
}

.s-companions__page-navigation-num--active {
  background-color: $light-gold;
}

.s-companions__page-navigation-arrows {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  button {
    display: flex;
    padding: 15px 20px;
  }
  button:first-child {
    margin-right: 100px;
  }
}

@media (min-width: $tablet-layout-width) {
  .s-companions {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .s-companions__main-wrap {
    padding: 0 20px;
  }

  .s-companions__filter-wrap {
    margin-bottom: 40px;
    padding: 37px 30px;
    h3 {
      font-size: 30px;
      margin-bottom: 35px;
    }
  }

  .s-companions-form__flex-container {
    display: flex;
    align-items: flex-start;
    padding: 14px 0;
  }

  .s-companions-form__fake-legend {
    width: 125px;
    padding: 0;
    border: none;
    font-size: 20px;
    &::after {
      display: none;
    }
  }

  .s-companions-form__item {
    width: 160px;
    margin-bottom: 12px;
    label {
      font-size: 20px;
      padding-left: 36px;
      &::before {
        width: 24px;
        height: 24px;
      }
    }
    input:checked + label::before {
      background-size: 12px 12px;
    }
  }

  .s-companions-form__svg-plane {
    width: 19px;
    height: 21px;
  }

  .s-companions-form__svg-bus {
    width: 17px;
    height: 21px;
  }

  .s-companions-form__svg-bike {
    width: 24px;
    height: 21px;
  }

  .s-companions-form__svg-afoot {
    width: 20px;
    height: 21px;
  }

  .s-companions-form__hobby-set {
    border-top: 1px solid #f3c72b;
    padding-top: 20px;
  }

  .s-companions-form__level {
    border-bottom: 1px solid #f3c72b;
    padding-bottom: 35px;
    .s-companions-form__flex-container {
      align-items: center;
    }
  }

  .s-companions-form__transport-set {
    .s-companions-form__flex-container {
      align-items: center;
    }
  }

  .s-companions-form {
    h3 {
      display: inline-block;
      width: auto;
      margin: 0;
    }
    .s-companions-form__list {
      width: calc(100% - 125px);
      display: flex;
      flex-wrap: wrap;
      margin: 0;
    }
  }

  .s-companions-form__show-result-btn {
    margin-top: 35px;
    border-radius: 25px;
    font-size: 20px;
    padding: 17px;
  }

  .s-companions-form {
    .s-companions-form__level-nums, .s-companions-form__range {
      margin-bottom: 0;
    }
  }

  .s-companions-form__level-nums {
    order: 1;
    width: 25%;
    margin-left: auto;
    input {
      font-size: 20px;
    }
    li {
      width: 75px;
    }
  }

  .s-companions-form__range {
    width: 45%;
  }

  .s-companions__traveler-card {
    flex-direction: row;
    align-items: flex-start;
    padding-top: 35px;
    padding-bottom: 25px;
    margin-bottom: 40px;
    min-height: 360px;
  }

  .s-companions__traveler-img {
    position: absolute;
    margin-left: 35px;
    margin-right: 0;
    img {
      width: 190px;
      height: 190px;
      border-radius: 20px;
      margin-right: 30px;
    }
  }

  .s-companions__traveler-name {
    margin-left: 260px;
    margin-bottom: 10px;
    h4 {
      font-size: 30px;
    }
  }

  .s-companions__tags {
    width: 100%;
    margin-left: 260px;
    margin-top: 0;
    margin-bottom: 20px;
    padding-right: 100px;
    li {
      font-size: 20px;
    }
  }

  .s-companions__desired-countries {
    width: 100%;
    margin-left: 35px;
    margin-right: 35px;
    margin-bottom: 0;
    order: 3;
  }

  .s-companions__desired-countries-title {
    margin-bottom: 20px;
  }

  .s-companions__desired-countries-list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
    li {
      display: flex;
      align-items: center;
      margin-right: 25px;
      span {
        text-transform: uppercase;
        font-weight: 500;
        margin-left: 10px;
        font-size: 20px;
      }
      img {
        width: 35px;
        height: 24px;
      }
    }
  }

  .s-companions__traveler-likes-and-btn {
    order: 1;
    margin-left: 260px;
    margin-top: auto;
    align-items: center;
    z-index: 2;
  }

  .s-companions__traveler-card-btn {
    width: auto;
    padding: 15px 30px;
    margin: 0;
    font-size: 20px;
  }

  .s-companions__traveler-like {
    width: 50px;
    height: 50px;
    margin-right: 8px;
    svg {
      width: 20px;
      height: 17px;
    }
  }

  .s-companions__traveler-likes {
    position: static;
    span {
      font-size: 20px;
    }
  }

  .s-companions__transport-and-level {
    position: absolute;
    right: 35px;
    order: 2;
    flex-direction: column;
    align-items: center;
  }

  .s-companions__transport-wrap {
    flex-grow: 0;
  }

  .s-companions__traveler-level {
    margin-right: 0;
    margin-top: 15px;
  }

  .s-companions__traveler-level-ctn {
    svg {
      width: auto;
      height: auto;
    }
  }

  .s-companions__traveler__num {
    top: 35%;
    color: $dark-blue;
  }

  .s-companions__traveler__text {
    display: block;
    font-size: 14px;
    color: rgba(68, 68, 68, 0.4);
    border-top: 1px solid rgba(68, 68, 68, 0.4);
    padding-top: 3px;
  }

  .s-companions__transport-title, .s-companions__traveler-level-title {
    display: none;
  }

  .s-companions__transport-list {
    flex-direction: column;
    width: auto;
    margin: 0;
    li {
      background-color: transparent;
    }
    .icon-plane-svg {
      width: 19px;
      height: 21px;
    }
    .icon-bus-svg {
      width: 17px;
      height: 21px;
    }
    .icon-bike-svg {
      width: 24px;
      height: 21px;
    }
    .icon-foot-svg {
      width: 20px;
      height: 21px;
    }
  }

  .s-companions__travelers-btn-view-more {
    font-size: 20px;
    margin-top: 37px;
    margin-bottom: 40px;
    span {
      width: 27px;
      height: 27px;
    }
  }

  .s-companions__page-navigation {
    justify-content: space-between;
    padding-top: 25px;
    padding-bottom: 25px;
  }

  .s-companions__page-navigation-nums {
    display: flex;
  }

  .s-companions__page-navigation-arrows button:first-child {
    margin-right: 5px;
  }

}

@media (min-width: $desktop-layout-width) {
  .s-companions {
    padding-top: 0;
  }

  .s-companions__main-wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .s-companions__filter-wrap {
    order: 1;
    padding: 47px;
    margin-bottom: 0;
    margin-left: 85px;
  }

  .s-companions-form__flex-container {
    flex-direction: column;
    justify-content: flex-start;
  }

  .s-companions-form__hobby-set {
    border: none;
    padding-top: 0;
  }

  .s-companions-form__fake-legend {
    width: 100%;
    margin-bottom: 20px;
    &:after {
      display: block;
    }
  }

  .s-companions-form__level .s-companions-form__flex-container,
  .s-companions-form__transport-set .s-companions-form__flex-container {
    align-items: flex-start;
  }

  .s-companions-form .s-companions-form__list {
    width: auto;
  }

  .s-companions-form__level {
    border: none;
    padding-bottom: 0;
  }

  .s-companions-form__range {
    width: 100%;
    order: 2;
    margin-top: 35px;
  }

  .s-companions-form__level-nums {
    width: 100%;
    justify-content: center;
    li {
      width: calc(50% - 1px);
    }
  }

  .s-companions__traveler-card {
    align-items: flex-start;
    padding-right: 45px;
    min-height: 285px;
    padding-top: 45px;
    padding-bottom: 45px;
  }

  .s-companions__transport-and-level {
    position: static;
    flex-direction: row;
    order: 3;
    margin-top: auto;
    margin-left: auto;
    margin-bottom: -5px;
  }

  .s-companions__traveler-level {
    margin-top: 0;
    line-height: 0;
  }

  .s-companions__desired-countries {
    order: 1;
    width: auto;
    margin-left: 680px;
    margin-top: -125px;
    margin-bottom: 0;
    margin-right: 0;
    .s-companions__desired-countries-list {
      margin-right: 0;
    }
  }

  .s-companions__desired-countries-list {
    flex-direction: column;
  }

  .s-companions__desired-countries-title {
    display: none;
  }

  .s-companions__transport-list {
    flex-direction: row;
    margin-right: 55px;
  }

  .s-companions__traveler-likes-and-btn {
    order: 2;
    margin-left: 340px;
    margin-top: auto;
  }

  .s-companions__traveler-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 285px;
    height: 100%;
    margin: 0;
    img {
      margin: 0;
      width: 100%;
      height: 100%;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .s-companions__traveler-name {
    margin-left: 330px;
    h4 {
      padding-left: 18px;
    }
  }

  .s-companions__traveler-status {
    right: auto;
    left: 0;
  }

  .s-companions__tags {
    width: 225px;
    padding-right: 0;
    margin-left: 340px;
  }

}
