.s-travel-companions {
  background-color: $light-grey;
}

.s-travel-companions__bg-wrap {
  position: relative;
  background-color: $dark-blue-grey;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background-image: url("../../img/smolov-back-desktop@1x.png"), url("../../img/rogovaya-back-mobile@1x.png");
  background-position: 150px 40px, -10px 320px;
  background-repeat: no-repeat;
  p {
    font-weight: 300;
    margin-bottom: 27px;
  }
  strong {
    font-weight: bold;
  }
}

.s-travel-companions__decorative-rect {
  position: absolute;
  top: 40px;
  left: -105px;
  width: 213px;
  height:132px;
  border-radius: 20px;
  background-color: #141b3b;
  z-index: 1;
}

.s-travel-companions__list {
  margin-top: 60px;
}

.s-travel-companions__item {
  margin-bottom: 10px;
}

.s-travel-companions__view-all-btn {
  margin-top: 20px;
}

.s-travel-companions__traveler-level circle {
  fill: #000;
  fill-opacity: 0.5;
  stroke-width: 2px;
  stroke: $mango;
}

.s-travel-companions__level-num, .s-travel-companions__level-text {
  color: #fff;
}

.s-travel-companions__level-num {
  font-size: 24px;
  font-weight: bold;
  &::after {
    content: "";
    background-color: rgba($color: #fff, $alpha: .25);
  }
}

.s-travel-companions__level-text{
  opacity: .6;
  font-weight: 300;
  font-size: 14px;
}

.s-travel-companions__user-foto {
  position: relative;
}

.s-travel-companions__traveler-level {
  position: absolute;
  top: 14px;
  left: 15px;
}

@media (min-width: $tablet-layout-width) {
  .s-travel-companions__bg-wrap {
    padding-top: 140px;
    background-image: url("../../img/smolov-back-desktop@1x.png"), url("../../img/rogovaya-back-desktop@1x.png");
    background-position: 45px 430px, 420px 70px;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    .caption-indent {
      width: 75%;
      p {
        font-size: 28px;
        line-height: 1.18;
        margin-top: 60px;
        margin-bottom: 45px;
      }
      strong {
        font-size: 21px;
        line-height: 1.18;
      }
    }
  }

  .s-travel-companions__decorative-rect {
    top: 70px;
    left: 45px;
    height: 213px;
  }

  .s-travel-companions__list {
    display: flex;
    justify-content: space-between;
    margin-left: 50px;
    margin-right: 50px;
  }

  .s-travel-companions__item {
    flex-basis: 46%;
  }

  .s-travel-companions__view-all-btn {
    width: 86%;
    margin: 0 auto;
    margin-top: 80px;
  }
}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .s-travel-companions__bg-wrap {
    background-position: 550px 140px, 200px 410px;
    .caption-indent {
      width: 45%;
      padding-left: 115px;
      strong {
        display: inline-block;
        padding-right: 30%;
      }
    }
  }

  .s-travel-companions__ctn-wrap {
    display: flex;
  }

  .s-travel-companions__list {
    width: 47%;
    margin: 0;
    margin-right: 35px;
    margin-left: auto;
    justify-content: space-around;
  }

  .s-travel-companions__item {
    flex-basis: 42%;
  }

  .s-travel-companions__view-all-btn {
    position: relative;
    bottom: 60px;
    width: 20%;
    margin: 0;
    margin-left: 115px;
  }

  .companion-card__hobbies {
    margin-top: 5px;
    margin-bottom: 20px;
  }
}
