.btn {
  display: block;
  border-radius: 10px;
  font-size: 17px;
  font-weight: bold;
  line-height: 1.76;
  color: $dark-text-color;
  &:hover {
    cursor: pointer;
  }
}

.btn--add {
  width: 100%;
  background-color: $light-gold;
  border-radius: 20px;
  padding: 12px;
  text-align: center;
  text-transform: uppercase;
}

.btn--main {
  width: 100%;
  position: relative;
  font-size: 18px;
  padding: 24px;
  &::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    top: 50%;
    right: 24px;
    transform: translateY(-50%);
    background-image: url("../../img/triangle-arrow-right.svg");
    background-repeat: no-repeat;
    background-size: 10px 14px;
    background-position: center;
  }
}

.btn--light-gold {
  background-color: $light-gold;
}

.btn--border-dark-blue {
  border: 3px solid $dark-blue-grey;
}

.btn--add-plane-step-next, .btn--add-plane-step-prev, .btn--add-plane-send {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 40px;
  padding: 7px;
  margin-top: 10px;
  text-align: center;
  font-size: 17px;
  text-transform: uppercase;
  background-color: $light-gold;
  border-radius: 20px;
}

.btn--add-plane-step-prev {
  background-color: transparent;
}

// media queries for tablet
@media (min-width: $tablet-layout-width) {
  .btn--main {
    font-size: 24px;
    padding: 32px;
  }
  .btn--add-plane-step-next, .btn--add-plane-step-prev, .btn--add-plane-send {
    width: 55%;
    min-height: 80px;
    padding-right: 30px;
    padding-left: 30px;
    position: relative;
    font-size: 24px;
    text-align-last: left;
    text-transform: none;
    border-radius: 20px;
    &::after {
      content: "";
      width: 10px;
      height: 14px;
      position: absolute;
      right: 33px;
      top: 50%;
      margin-top: -7px;
      background-image: url("../img/triangle-arrow-right.svg");
      background-repeat: no-repeat;
    }
  }
  .btn--add-plane-step-prev {
    width: 35%;
    margin-left: auto;
    padding-right: 0;
    padding-left: 30px;
    &::after {
      right: auto;
      left: -5px;
      padding: 0;
      background-image: url("../img/triangle-arrow-left.svg");
    }
  }
}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .btn--main {
    border-radius: 20px;
  }
  .btn--add-plane-step-next, .btn--add-plane-step-prev, .btn--add-plane-send {
    width: 30%;
  }
}

// :hover, :focus, :active etc.
.btn--add-plane-step-next, .btn--add-plane-send {
  @include transition(color);
  @include transition(background-color);
  &:hover, &:focus {
    outline: none;
    background-color: $light-mango;
    @include transition(background-color);
  }
  &:active {
    color: rgba($dark-blue, 0.3);
    @include transition(color);
    &:after {
      opacity: 0.3;
      @include transition(opacity);
    }
  }
  &:after {
    @include transition(opacity);
  }
}

.btn--add-plane-step-prev {
  @include transition(opacity);
  &:hover, &:focus {
    outline: none;
    opacity: 0.7;
    @include transition(opacity);
  }
  &:active {
    opacity: 0.3;
    @include transition(opacity);
  }
}
