.s-about-features {
  background-color: $dark-blue-grey;
  padding-top: 50px;
  padding-bottom: 40px;
}

.s-about-features__list {
  margin-top: 33px;
}

.s-about-features__quote {
  width: 155px;
  font-weight: bold;
}

.s-about-features__quote q {
  display: inline-block;
  font-weight: 300;
  margin-bottom: 25px;
}

.s-about-features__item {
  position: relative;
  font-weight: bold;
  padding: 23px 105px 25px 25px;
  line-height: 1;
  font-size: 18px;
  &::after{
    position: absolute;
    color: #fff;
    top: 23px;
    right: 21px;
  }

}

.s-about-features__item:nth-child(1) {
  background-color: $mango;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  &::after{
    content: "1";
  }
}

.s-about-features__item:nth-child(2) {
  background-color: #FFA62D;
  &::after{
    content: "2";
  }
}

.s-about-features__item:nth-child(3) {
  background-color: $light-mango;
  &::after{
    content: "3";
  }
}

.s-about-features__item:nth-child(4) {
  background-color: $light-gold;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  &::after{
    content: "4";
  }
}

// media queries for tablet
@media (min-width: $tablet-layout-width) {
  .s-about-features {
    height: 200px;
  }
  .s-about-features__wrap {
    position: relative;
    top: -590px;
    z-index: 2;
  }

  .s-about-features__list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 245px;
    padding: 0 15px;
  }

  .s-about-features__item {
    flex-basis: 50%;
    font-size: 24px;
    padding-bottom: 60px;
    &::after {
      top: auto;
      right: auto;
      left: 23px;
      bottom: 25px;
    }
  }

  .s-about-features__quote {
    width: 260px;
    line-height: 1.19;
    margin-bottom: 0;
    q {
      font-size: 28px;
    }
    cite {
      font-size: 21px;
    }
  }

  .s-about-features__item:nth-child(1) {
    border-top-left-radius: 10px;
    border-top-right-radius: 0;
  }

  .s-about-features__item:nth-child(2) {
    border-top-right-radius: 10px;
  }

  .s-about-features__item:nth-child(3) {
    border-bottom-left-radius: 10px;
  }

  .s-about-features__item:nth-child(4) {
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 0;
  }

  .s-about-features__phrase-author {
    display: inline-block;
    width: 100%;
  }

}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .s-about-features {
    height: 0;
    padding: 0;
  }
  .s-about-features__wrap {
    top: -530px;
  }

  .s-about-features__quote {
    margin-left: auto;
  }

  .s-about-features__list {
    margin-top: 115px;
  }

  .s-about-features__item {
    flex-basis: 25%;
    padding-top: 30px;
    padding-bottom: 35px;
  }

  .s-about-features__item:nth-child(1) {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .s-about-features__item:nth-child(2) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  .s-about-features__item:nth-child(3) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  .s-about-features__item:nth-child(4) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }

  .s-about-features__item::after {
    top: 30px;
    right: 34px;
    bottom: auto;
    left: auto;
  }
}
