.main-header__bg-wrap {
  position: relative;
  background-color: $dark-text-color;
  background-image: url("../../img/globe-back-mobile@1x.jpg");
  background-position: left bottom 75%;
  background-size: cover;
  background-repeat: no-repeat;
}

.main-header h1 {
  width: 250px;
  text-align: center;
  padding: 20px 0px 270px;
  margin: 0 auto;
  font-weight: bold;
  line-height: 1.11;
}

.main-header__topline-menu {
  display: flex;
  justify-content: space-between;
  margin-right: -25px;
  margin-left: -25px;
  padding-left: 25px;
  padding-right: 25px;
  align-items: center;
  height: 73px;
  background-color: #fff;
}

.main-header__topline-menu--dark {
  background-color: transparent;
  .main-header__logo-img-blue {
    display: none;
  }
}

.main-header__logo {
  width: 100px;
  height: 19px;
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  img {
    width: 100%;
    height: 100%;
    position: absolute;
  }
}

.main-header__toggle-menu-btn {
  display: none;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  &:hover {
    cursor: pointer;
  }
  &--close, &--open {
    display: block;
  }
  &--close {
    width: 19px;
    height: 19px;
    background-image: url("../../img/menu-close.svg");
  }
  &--open {
    width: 25px;
    height: 11px;
    background-image: url("../../img/menu.svg");
  }
}

.main-header__menu {
  padding: 45px 25px 40px;
  margin-left: -25px;
  margin-right: -25px;
  position: relative;
  z-index: 2;
  background-color: #fff;
}

.main-header__auth-btn {
  margin-bottom: 50px;
  border-radius: 50px;
  font-size: 17px;
  height: 45px;
  line-height: 1.5;
  font-weight: bold;
  text-transform: uppercase;
}

.main-header__menu--close {
  padding: 0;
  background-color: transparent;
  .main-header__nav-links,
  .main-header__social-btns {
    display: none;
  }
  .main-header__btns {
    display: flex;
    position: absolute;
    left: 25px;
    right: 25px;
    top: 385px;
  }
  .main-header__auth-btn {
    order: 1;
    margin: 0;
  }
  .main-header__contacts {
    display: flex;
    margin: 0;
    .main-header__btn-text {
      display: none;
    }
  }
  .main-header__phone-btn .main-header__svg-wrap {
    background-color: $mango;
  }
  .main-header__mail-btn .main-header__svg-wrap {
    background-color: $light-mango;
  }
  .main-header__svg-wrap {
    svg {
      fill: $dark-blue-grey;
    }
  }
}

.main-header__nav-links {
  margin-bottom: 55px;
}

.main-header__nav-links a {
  display: block;
  text-transform: uppercase;
  text-align: center;
  color: $dark-blue-grey;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.2;
}

.main-header__contacts {
  margin-bottom: 40px;
}

.main-header__phone-btn, .main-header__mail-btn {
  display: flex;
  a {
    display: flex;
    align-items: center;
  }
}

.main-header__svg-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50px;
  line-height: 50px;
  &:hover {
    svg {
      fill: $light-mango;
      transition: $default-transition;
    }
  }
}

.main-header__svg-wrap svg {
  fill: $light-gold;
  transition: $default-transition;
}

.main-header__btn-text {
  font-weight: bold;
  color: $dark-blue-grey;
  text-transform: uppercase;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
}

.main-header__social-btns {
  display: flex;
  justify-content: center;
}

.main-header__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border-radius: 60px;
  background-color: $light-gold;
  transition: $default-transition;
  &:hover {
    background-color: $dark-blue-grey;
    transition: $default-transition;
    svg {
      fill: #fff;
    }
  }
}

.main-header__social-link svg {
  fill: $dark-blue-grey;
}

.main-header__ctn-wrap {
  position: relative;
}

.main-header__traveller-img {
  position: absolute;
  bottom: 0;
  right: 10%;
  z-index: 1;
}

// media queries for tablet
@media (min-width: $tablet-layout-width) {
  .main-header__bg-wrap {
    background-color: #182044;
    background-image: url("../../img/globe-back-tablet@1x.jpg");
    background-position: top 320px center;
    background-size: cover;
  }

  .main-header h1 {
    width: 400px;
    font-size: 60px;
    padding-top: 50px;
    padding-bottom: 630px;
  }

  .main-header__traveller-img {
    width: 372px;
    height: 451px;
    right: 15%;
    bottom: 128px;
  }

  .main-header__menu {
    padding-left: 95px;
    padding-top: 60px;
    padding-bottom: 90px;
  }

  .main-header__topline-menu {
    height: 111px;
  }

  .main-header__toggle-menu-btn {
    margin-right: 345px
  }

  .main-header__nav-links a {
    display: inline-block;
    font-size: 60px;
  }

  .main-header__btn-text {
    font-size: 28px;
    padding-left: 24px;
  }

  .main-header__svg-wrap {
    width: 67px;
    height: 67px;
  }

  .main-header__phone-btn svg {
    width: 27px;
    height: 27px;
  }

  .main-header__mail-btn svg {
    width: 27px;
    height: 19px;
  }

  .main-header__social-btns {
    justify-content: flex-start;
    padding-left: 5px;
  }

  .main-header__social-link {
    width: 67px;
    height: 67px;
    &--facebook svg{
      width: 14px;
      height: 28px;
    }
    &--vkontakte svg{
      width: 31px;
      height: 16px;
    }
    &--instagram svg{
      width: 24px;
      height: 24px;
    }
  }

  .main-header__logo {
    width: 166px;
    height: 42px;
  }

  .main-header__auth-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 40px;
    position: absolute;
    top: -80px;
    right: 40px;
    width: auto;
    font-size: 20px;
    height: 50px;
  }

  .main-header__menu--close .main-header__auth-btn {
    position: static;
  }

  .main-header__menu--close .main-header__btns {
    width: auto;
    position: absolute;
    top: -80px;
    right: 40px;
    left: auto;
  }

  .main-header__menu--close .main-header__contacts {
    display: none;
  }

  .main-header__menu--close {
    padding: 0;
  }

}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .main-header__bg-wrap {
    background-color: #161c35;
    background-image: url("../../img/globe-back-desktop@1x.jpg");
    background-position: top 0px left -200px;
  }

  .main-header h1 {
    width: 540px;
    margin: 0;
    line-height: 1.18;
    text-align: left;
    padding-top: 100px;
    padding-left: 105px;
    padding-bottom: 340px;
  }

  .main-header__topline-menu {
    height: 120px;
    padding-top: 15px;
    padding-left: 35px;
    padding-right: 35px;
  }

  .main-header__logo {
    width: 200px;
    height: 52px;
  }

  .main-header__toggle-menu-btn {
    display: none;
  }

  .main-header__menu {
    background-color: transparent;
    padding: 0;
  }

  .main-header__btn-text {
    display: none;
  }

  .main-header__btns {
    display: flex;
    position: absolute;
    top: -80px;
    right: 40px;
    left: auto;
  }

  .main-header__auth-btn {
    position: static;
    margin-bottom: 0;
    order: 1;
  }

  .main-header__menu--close .main-header__contacts, .main-header__contacts {
    display: flex;
    margin-bottom: 0;
  }

  .main-header__svg-wrap {
    width: 50px;
    height: 50px;
    svg {
      fill: $dark-text-color;
    }
  }

  .main-header__phone-btn svg {
    width: 18px;
    height: 18px;
  }

  .main-header__mail-btn svg {
    width: 18px;
    height: 14px;
  }

  .main-header__phone-btn {
    .main-header__svg-wrap {
      background-color: $mango;
    }
  }

  .main-header__mail-btn {
    .main-header__svg-wrap {
      background-color: $light-mango;
    }
  }

  .main-header__menu--close .main-header__nav-links, .main-header__nav-links {
    display: flex;
  }

  .main-header__nav-links a {
    color: #fff;
    font-size: 20px;
    font-weight: 500;
  }

  .main-header__nav-links {
    display: flex;
    flex-wrap: wrap;
    max-width: 670px;
    bottom: 65px;
    left: 375px;
    position: relative;
    z-index: 5;
    margin-bottom: 0;
    align-items: center;
  }

  .main-header__social-btns {
    display: none;
  }

  .main-header__nav-links li {
    padding-left: 36px;
    margin-bottom: 7px;
    &:first-child a {
      &::before {
        content: "";
        position: absolute;
        top: 12px;
        right: 100%;
        transform: translateY(-50%);
        width: 60px;
        height: 2px;
        background-color: #fff;
      }
    }
  }

  .main-header__traveller-img {
    width: 431px;
    height: 640px;
    bottom: 0;
    right: 27%;
  }
}

// :hover, :focus, :active etc.
.main-header__menu--close .main-header__svg-wrap {
  @include transition(background-color);
  &:hover {
    svg {
      fill: $dark-blue-grey;
    }
  }
  &:active {
    svg {
      opacity: 0.3;
    }
  }
}

.main-header__mail-btn {
  &:hover .main-header__svg-wrap {
    background-color: $mango;
    @include transition(background-color);
  }
}

.main-header__phone-btn {
  &:hover .main-header__svg-wrap {
    background-color: $dark-mango;
    @include transition(background-color);
  }
}

.main-header__menu--close .main-header__auth-btn {
  @include transition(background-color);
  &:hover {
    background-color: $light-mango;
    @include transition(background-color);
  }
  &:active {
    color: rgba($dark-text-color, 0.3);
  }
}

.main-header__nav-links a {
  &:hover, &:focus {
    color: $light-gold;
    @include transition(color);
  }
  &:active {
    color: rgba($light-gold, 0.3);
  }
}
