.s-about-service {
  background-color: $light-grey;
}

.s-about-service__bg-wrap {
  background-color: $dark-blue-grey;
  background-image: url("../../img/about-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.s-about-service__ctn-wrap {
  padding-top: 33px;
  p {
    font-weight: 300;
  }
  strong {
    display: inline-block;
    margin-top: 25px;
    margin-bottom: 54px;
  }
  button {
    display: none;
  }
}

// media queries for tablet
@media (min-width: $tablet-layout-width) {
  .s-about-service {
    position: relative;
    background-color: $dark-blue-grey;
  }

  .s-about-service__decorative-rect {
    position: absolute;
    top: 10px;
    left: 40px;
    width: 76%;
    height: 213px;
    border-radius: 20px;
    background-color: #141b3b;
    z-index: 0;
  }

  .s-about-service__bg-wrap {
    position: relative;
    background-color: transparent;
    background-image: url("../../img/about-tablet@1x.png");
    background-size: 378px 548px;
    background-position: top 55px right -30px;
    z-index: 1;
  }

  .s-about-service__ctn-wrap {
    padding-top: 43px;
    padding-bottom: 90px;
    p {
      font-size: 28px;
      line-height: 1.18;
    }
    strong {
      font-size: 21px;
      line-height: 1.19;
      margin-top: 35px;
    }
    button {
      display: block;
      text-align: left;
    }
    .caption-indent {
      padding-right: 366px;
    }
  }
}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .s-about-service__bg-wrap {
    background-image: url("../../img/about-desktop@1x.png");
    background-size: 630px 665px;
    background-position: top 100% right 60px;
  }

  .s-about-service__ctn-wrap {
    padding-top: 130px;
    padding-bottom: 165px;
    h2 {
      margin-bottom: 60px;
    }
    .caption-indent {
      padding-right: 650px;
    }
    strong {
      padding-right: 170px;
      margin-top: 45px;
      margin-bottom: 85px;
    }
    button {
      width: 45%;
    }
  }

  .s-about-service__decorative-rect {
    width: 213px;
    top: 70px;
    left: 60px;
  }

  .s-about-service__ctn-wrap .caption-indent {
    padding-left: 118px;
  }
}
