.traveler-level {
  position: relative;
  display: inline-flex;
}

.traveler-level circle {
  fill: none;
  stroke-width: 2px;
  stroke: $mango;
}

.traveler-level__num, .traveler-level__text {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  line-height: 1;
  color: #000;
}

.traveler-level__num {
  top: 15%;
  font-weight: bold;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    bottom: -2px;
    height: 1px;
    background-color: #000;
  }
}

.traveler-level__text {
  top: 55%;
}
