.s-padding {
  padding-top: 70px;
  padding-bottom: 30px;
}

.h2-main {
  margin-bottom: 23px;
  line-height: 1;
  font-size: $default-font-h2;
  color: $mango;
  font-weight: bold;
}

.white-text {
  color: $white-text-color;
}

.container {
  max-width: 1440px;
  min-width: 320px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 25px;
  padding-right: 25px;
}

.caption-indent {
  padding-right: 35px;
  padding-left: 20px;
}

.caption-indent > * {
  position: relative;
  z-index: 2;
}

.img-responsive {
  display: block;
	max-width: 100%;
	height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.display-none {
  display: none;
}

.error-required-field {
  display: none;
  padding: 10px 15px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 10;
  font-size: 15px;
  line-height: 1.1;
  color: #ff0000;
  background-color: #ffefef;
}

// media queries for tablet
@media (min-width: $tablet-layout-width) {
  .h2-main {
    font-size: 45px;
    margin-bottom: 45px;
  }

  .caption-indent {
    padding-left: 50px;
  }

  .s-padding {
    padding-top: 75px;
    padding-bottom: 80px;
  }

  .error-required-field {
    font-size: 20px;
    padding: 20px 24px;
  }
}

@media (min-width: $desktop-layout-width) {
  .error-required-field {
    width: 80%;
  }
}
