.s-route__set-route {
  background-color: transparent;
}

.s-route__bg-wrap {
  background-color: $light-gold;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  padding: 35px 0;
  a {
    display: flex;
  }
}

.s-route__img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #121936;
  width: 45px;
  height: 45px;
  border-radius: 100%;
  margin-right: 15px;
  svg {
    fill: $light-gold;
  }
}

.s-route__text-wrap {
  flex-basis: 60%;
  color: $dark-blue-grey;
  font-weight: bold;
  text-transform: uppercase;
}

.s-route__btn {
  text-align: left;
  margin-top: 32px;
}

.s-route__map {
  position: relative;
  top: -18px;
  z-index: -1;
}

.s-route__map-iframe {
  width: 100%;
}

@media (min-width: $tablet-layout-width) {
  .s-route__bg-wrap {
    padding-top: 45px;
    padding-bottom: 85px;
    a {
      align-items: center;
    }
  }

  .s-route__ctn-wrap {
    padding: 0 50px;
  }

  .s-route__text-wrap {
    font-size: 28px;
  }

  .s-route__img-wrap {
    width: 67px;
    height: 67px;
    margin-right: 25px;
  }

  .s-route__img-wrap svg {
    width: 21px;
    height: 31px;
  }
}

@media (min-width: $desktop-layout-width) {
  .s-route__bg-wrap {
    padding-top: 65px;
    padding-bottom: 60px;
  }
  .s-route__ctn-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 135px 0 300px;
  }

  .s-route__btn {
    flex-basis: 32%;
    margin-top: 0;
  }

  .s-route__text-wrap {
    flex-basis: auto;
  }

  .s-route__img-wrap {
    width: 90px;
    height: 90px;
    svg {
      width: 28px;
      height: 43px;
    }
  }
}
