@import "./reset-default-styles.scss";
@import "./vars.scss";
@import "./fonts.scss";
@import "./helpers.scss";

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  min-width: $mobile-layout-width;
  font-family: $default-font;
  font-size: $default-font-size;
  box-sizing: border-box;
  line-height: $default-line-height;
  color: $dark-text-color;
}

a {
  text-decoration: none;
  &:hover {
    cursor: pointer;
  }
}

h1 {
  font-size: $default-font-h1;
}

h3 {
  font-size: $default-font-h3;
}

ul {
  padding: 0;
}

button {
  display: block;
  border: none;
  padding: 0;
  margin: 0;
  background-color: transparent;
  font-family: $default-font;
  box-sizing: inherit;
  &:hover {
    cursor: pointer;
  }
}

