.s-add-plan {
  background-color: #dfe3f0;
}

.s-add-plan-inner {
  padding: 30px 0;
  background-color: #d4d9eb;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  h3 {
    display: flex;
    margin-bottom: 22px;
  }
}

.s-add-plan-inner__caption-wrap {
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.s-add-plan-inner__small-nav-btns {
  margin-right: 7px;
}

.s-add-plan-inner__small-nav-btn {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 5px;
  margin: 0 5px;
  margin-top: 7px;
  border-radius: 50%;
  background-color: #808285;
  opacity: 0.3;
  &--active {
    opacity: 1;
    background-color: $dark-blue;
  }
}

@media (min-width: $tablet-layout-width) {
  .s-add-plan-inner {
    padding: 50px;
    h3 {
      margin-bottom: 42px;
      font-size: 36px;
    }
  }

}

