.s-destinations {
  background-color: $dark-blue-grey;
}

.s-destinations__bg-wrap {
  background-color: $light-grey;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  p {
    color: #444444;
  }
}

.s-destinations__list {
  margin-top: 60px;
  margin-bottom: 13px;
}

.s-destinations__item {
  display: flex;
  flex-direction: column;
}

.s-destinations__item:last-child {
  margin-bottom: 0;
  .s-destinations__descr {
    background: linear-gradient(to bottom, #fff 50%, $light-grey 100%);
  }
}

.s-destinations__img-wrap img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.s-destinations__descr {
  position: relative;
  top: -12px;
  display: flex;
  flex-wrap: wrap;
  border-radius: 10px;
  background-color: #fff;
  padding: 23px;
  h4 {
    font-weight: bold;
    font-size: 21px;
    line-height: 1;
  }
  small {
    position: relative;
    color: rgba(68, 68, 68, 0.3);
    font-weight: 500;
    line-height: 1.88;
    margin-left: auto;
    padding-left: 18px;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 11px;
      height: 11px;
      background-image: url("../../img/user.svg");
    }
  }
  p {
    width: 100%;
    font-weight: 500;
    line-height: 1.13;
    color: rgba(68, 68, 68, 0.3);
  }
}

.s-destinations__falg-img {
  position: absolute;
  top: -12px;
  left: 25px;
  width: 35px;
  height: 24px;
}

@media (min-width: $tablet-layout-width) {
  .s-destinations__bg-wrap {
    background-image: url("../../img/flags-back@1x.png");
    background-repeat: no-repeat;
    background-size: 289px 575px;
    background-position: 92% 70px;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    .caption-indent p {
      width: 58%;
      font-size: 28px;
      line-height: 1.18;
      margin-top: 60px;
      margin-bottom: 110px;
    }
  }

  .s-destinations__list {
    position: relative;
    padding-left: 50px;
    padding-right: 50px;
    &::before {
      content: "";
      position: absolute;
      top: -40px;
      left: 15px;
      width: 141px;
      height: 141px;
      border-radius: 20px;
      background-color: #e2e5f2;
    }
  }

  .s-destinations__item {
    flex-direction: row;
    margin-bottom: 10px;
  }

  .s-destinations__descr {
    order: -1;
    flex-grow: 1;
    flex-direction: column;
    top: auto;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    padding: 28px 10px 18px 30px;
    h4 {
      font-size: 24px;
    }
    small {
      font-size: 20px;
      line-height: 1.5;
      margin-left: 0;
      margin-top: 15px;
      padding-left: 30px;
      order: 4;
      &::before {
        width: 21px;
        height: 20px;
        left: 0;
      }
    }
    p {
      font-size: 20px;
      line-height: 1.3;
      width: auto;
      order: 3;
    }
  }

  .s-destinations__img-wrap {
    flex-basis: 186px;
  }

  .s-destinations__img-wrap img {
    border-top-left-radius: 0;
  }

  .s-destinations__falg-img {
    top: 15px;
    left: auto;
    right: -35px;
    width: 70px;
    height: 47px;
  }

  .s-destinations__view-all-btn {
    width: 85%;
    margin: 0 auto;
    margin-top: 45px;
  }

  .s-destinations__item:last-child .s-destinations__descr {
    background: #fff;
  }
}

// media queries for desktop
@media (min-width: $desktop-layout-width) {
  .s-destinations__bg-wrap {
    padding-top: 160px;
  }
  
  .s-destinations__tn-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .caption-indent {
      flex-basis: 40%;
      padding: 0;
      p {
        width: 65%;
      }
    }
  }

  .s-destinations__list {
    flex-basis: 40%;
    padding: 0;
    order: -1;
    margin: 0;
    margin-left: 110px;
    &::before {
      top: -65px;
      left: -65px;
    }
  }

  .s-destinations__view-all-btn {
    position: relative;
    bottom: 90px;
    width: 20%;
    margin: 0;
    margin-left: 60%;
    border-radius: 20px;
  }

  .s-destinations__item {
    position: relative;
    &:last-child {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to bottom, transparent 50%, #edeff6 100%);
        z-index: 5;
      }
    }
  }
}
